@import '../../../base';

.popup-with-text {
  .cont {
    max-width: 650px;
    padding-left: 15px;
    padding-right: 15px;

    .title {
      margin: 10px 0;
      color: $color-hover;
      font-size: $font-size-big * 0.9 !important;
      @media screen and (max-width: 576px) {
        font-size: $font-size-small !important;
      }
    }

    p {
      font-size: $font-size-small !important;
      font-weight: $font-weight-light !important;
      @media screen and (max-width: 576px) {
        font-size: $font-size-smallest !important;
      }
    }
  }
}